  export function ObjectKeys(p) {
    var r = [];
    if (p) {
      try {
        r = Object.keys(p);
      } catch (e) {
        for (var k in p) {
          r.push(k);
        }
      }
    }
    return r;
  }


  
export function gen16CodeId() {
    var ALPHABET =
      "0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwyxz";
    var ID_LENGTH = 16;
    var rtn = "";
    for (var i = 0; i < ID_LENGTH; i++) {
      rtn += ALPHABET.charAt(Math.floor(Math.random() * ALPHABET.length));
    }
    return rtn;
  }

 


