export function gen4NumCode() {
  var ALPHABET =
    "0123456789";
  var ID_LENGTH = 4;
  var rtn = "";
  for (var i = 0; i < ID_LENGTH; i++) {
    rtn += ALPHABET.charAt(Math.floor(Math.random() * ALPHABET.length));
  }
  return rtn;
}


export function genAlphCode() {
  var ALPHABETDD =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZ97531";
    var ALPHABETM =
    "DYFGXWJKLMZO";
    var mm = new Date().getMonth();
    var dd = new Date().getDate()-1;
  return ALPHABETM[mm] + ALPHABETDD[dd] + "-" + gen4NumCode() ;
}



