let storeCallbackOnMemory = {}


export function compareDependencies(name, params ) {
  let sum = 0
  params.forEach((dpn,ind2)=>{
    let v1 = storeCallbackOnMemory[name]["params"][ind2];
    if(typeof dpn === "object"){
      sum += JSON.stringify(v1)===JSON.stringify(dpn)?1:0;
    }
    else{
      sum += v1===dpn?1:0;
    }
  })
  return params.length === sum;
}



export function callbackOnMemory(name, params , cb ) {
  if(!storeCallbackOnMemory[name]){
    storeCallbackOnMemory[name] = {}
  }
  if(!storeCallbackOnMemory[name]["params"]){
    storeCallbackOnMemory[name]["params"] = params;
  }
  if(!storeCallbackOnMemory[name]["result"]){
    storeCallbackOnMemory[name]["result"] = cb();
  }
  
  if(compareDependencies(name, params )){
    return  storeCallbackOnMemory[name]["result"];
  }
  else{
    storeCallbackOnMemory[name]["params"] = params;
    storeCallbackOnMemory[name]["result"] = cb();
    return  storeCallbackOnMemory[name]["result"];
  }
}




