export const csvJSON = (csv) => {

  var lines=csv.split("\r").join("").split("\n");
 

  var result = [];

  var headers=lines[0].split(",");

  for(var i=1;i<lines.length;i++){

      var obj = {};
      var currentline=lines[i].split(",");
      if(currentline.length>headers.length){
          let _string =  lines[i].split('"');
          let nwStr = _string[1].split(',').join(";");
          currentline = lines[i].split(_string[1]).join(nwStr).split(",");
      }

      for(var j=0;j<headers.length;j++){
        let fl = headers[j].trim();
          obj[fl] = currentline[j];
      }

      result.push(obj);

  }

  return result;
}



export const parse4Dig = (n) =>{
  if(n>9990){
    return n
  }else if(n>999){
    return "00"+n
    
  }else if(n>99){
    return "00"+n
    
  }else if(n>9){
    return "000"+n
  }
  else if(n<10){
    return "0000"+n
  }
}



