import React from "react";
import ObserveEvent from "../hooks/observeEvent";
import { callDispatchEvent, updStore, getStore, gen16CodeId } from "../stores/Utils";

import './Toast.css';

const obsCode = "listToast_gLt754";

const ToastHRM = () => {
	const list = getStore("listToat") || {};
  const observeComponent = ObserveEvent(obsCode);
	let _list = Object.keys(list);
	const close = (Id) => {
		CloseToast({ id: Id });
	};

	return (
		<>
			{list &&
				_list.map((dg, indg) => {
					let dlg = list[dg];
					if (dlg && dlg.visible) {
						if (dlg.display) {
						}
						let data = dlg.data;
						let classT = "toast active";
						let Tstyle = { opacity: dlg.display ? 1 : 0 };
						let _text =
							data && data.text ? data.text : "sdgfdsfgsdfg";
						let toastButtonStyle = {},
							_buttonText_ = `Ok`;

						return (
							<div id={dg} className={"TtBase_Toast"} key={dg}>
								<div
									className={dlg.display ? classT : "toast"}
									style={Tstyle}>
									<div className="text">{_text}</div>
									<div className="actions">
										<button
											className="md-button md-ink-ripple"
											type="button"
											style={toastButtonStyle}
											onClick={() => close(dg)}>
											{_buttonText_}
										</button>
									</div>
								</div>
							</div>
						);
					}
				})}
		</>
	);
};

export default ToastHRM;

const cleanbase64 = (id) => {
	let cClss = id;
	let h = new RegExp("=", "g");
	cClss = cClss.replace(h, "");
	return cClss;
};






/*************************************************************************************************************/
/*************************************************************************************************************/
/*************************************************************************************************************/

export const OpenToast = (data) => {
  let list = getStore("listToat") || {};
  let Id = gen16CodeId();
  if (data["Id"]) {
    Id = data["Id"];
  }
  if (!list[Id]) {
    list[Id] = {};
  }
  list[Id]["visible"] = true;
  let _dataProps = {};
  if (!data["props"]) {
    _dataProps["modalID"] = Id;
  } else {
    _dataProps = data["props"];
    _dataProps["modalID"] = Id;
  }
  _dataProps["text"] = data.text;
  list[Id]["data"] = _dataProps;
  list[Id]["display"] = true;
  updStore("listToat", list);
  callDispatchEvent(obsCode);
  setTimeout(() => {
    CloseToast({ id: Id });
  }, data["timeout"] || 3000);
};

export const CloseToast = (data) => {
  let list = getStore("listToat") || {};
  let Id = data["id"];
  if (list && list[Id]) {
    list[Id]["display"] = false;
    updStore("listToat", list);
    callDispatchEvent(obsCode);
    setTimeout(() => {
      delete list[Id];
      updStore("listToat", list);
      callDispatchEvent(obsCode);
      
    }, 275);
  }
};
