import { useState, useEffect } from 'react'
import './App.css'
import { Customer_Cmpt } from './features/Customer';
import { DialogHRM_Cmpt, SvgIcon_Cmpt, Toast_Cmpt } from './components';
import {FloatingSideMenu_Cmpt} from './features/FloatingSideMenu';
import { init_icons, getStore, routeList, updStore, hasWritePrivilege, ObjectKeys, getSvgSymbols, startObserbingClicks } from './stores/Utils';
import GoogleSingIn from './services/googleSingIn';

import ObserveEvent from "./hooks/observeEvent";
import { Aaccountlist_Cmpt, ParameterizeBG_Cmpt } from './features/CuentasContables';
import { ComprobanteList_Cmpt } from './features/Comprobantes';
import { CollectList_Cmpt, ToPaylist_Cmpt } from './features/Cobros';
import {UserDashboard_Cmpt} from './features/Users';
import { Entrylist_Cmpt } from './features/ComprobanteParametrizados';
import { HashRouter, useRoutes, useLocation } from "react-router-dom";






import './features/CuentasContables/component/dashboard.css';
import { InventoryDashboard_Cmpt } from './features/Inventory';
import { EmployeesDashboard_Cmpt } from './features/Employees';
import { ProvidersDashboard_Cmpt } from './features/ProvidersCustomers';
import { InvoicesDashboard_Cmpt } from './features/Invoices';
import { PayChecksOnFuturesDashboard_Cmpt } from './features/PayChecksOnFuture';


const ComprobanteList = ComprobanteList_Cmpt();
const Aaccountlist = Aaccountlist_Cmpt()
const InventoryDashboard = InventoryDashboard_Cmpt()

const CobroList = CollectList_Cmpt()
const ToPaylist = ToPaylist_Cmpt()

const UserDashboard = UserDashboard_Cmpt();

const Entrylist = Entrylist_Cmpt();

const EmployeesDashboard = EmployeesDashboard_Cmpt();

const ProvidersDashboard = ProvidersDashboard_Cmpt();

const PayChecksOnFuturesDashboard = PayChecksOnFuturesDashboard_Cmpt();


const DialogHRM = DialogHRM_Cmpt()

const FloatingSideMenu = FloatingSideMenu_Cmpt()

const ParameterizeBG = ParameterizeBG_Cmpt()

const Svg = SvgIcon_Cmpt();

const Toast = Toast_Cmpt();


const InvoicesDashboard = InvoicesDashboard_Cmpt()


const Customer = Customer_Cmpt()


const App = () => {

  useEffect(() => {

    let now = (new Date())
    let initDate = (new Date("01/01/"+now.getFullYear())).getTime(); 
    let lastDate = (new Date("01/01/"+now.getFullYear()+1)).getTime() - 10;

    let mnth = now.getMonth() +1;
    let dt = (new Date((now.getMonth()+1)+"/01/"+now.getFullYear()));
    let initDateMP = dt.getTime();
		let lastDateMP = dt.setMonth(mnth) - 600;

    updStore("date_range_comps", {initDate,lastDate} )
    updStore("date_range_submayor", {initDate: initDateMP, lastDate: lastDateMP} );
    updStore("modernFontReport",true);
    // startObserbingClicks();
    return () => {
      //
    }
  }, [])

	return (
		<div
      theme={"92357239"} className={""}
			style={{ zIndex: 0, overflow: "hidden" }}
			className={"palette_body App light_theme"}>
				<HashRouter>
					<InitCmpt />
				</HashRouter>
		</div>
	);
};















function InitCmpt() {

  const [view, setView] = useState(6)

  const observeComponent = ObserveEvent("isLogged_987");


  let location = useLocation();
 
  
  useEffect(() => {
    init_icons();
    setView(routesIndex[location.pathname])
    return () => {
      //
    }
  }, [location.pathname, location.search])
  




  let routesIndex = {
    "/accounts":6,
    "/accounting-ledger":7,
    "/collect":14,
    "/pay":14,
    "/users-dashboard":10,
    "/notary":11,
    "/inventory":12,
    "/employees":13,
    "/providers-and-customers":14,
    "/invoices":16,
    "/parameterize-balance":6,
    "/pay-checks-on-future":14
  }




  let profileUser = getStore("profileUser")
  
  return (
    <div >
      {profileUser?.userId ? (
      <div 
        className={"_dsplFlx dashboard formContainer"}
        style={{
          width: "100%",
          "--menu-slide-wd": "242px",
          opacity: 1
        }}
      >
        <FloatingSideMenu  data={routeList()} active={view} updTab={setView}/>
        <div 
          className={"dashboard_body"}
          style={{
            opacity: 1,
            width: "calc(98vw - var(--menu-slide-wd))",
            minHeight: "96vh"
          }}
        >
            <RoutesH />
        </div>
      </div>
      ):
      <GoogleSingIn />}
      <DialogHRM />
      <Toast />
     
    
    </div>
  )
}

export default App






function RoutesH() {
	return useRoutes([
    { path: "/", element: hasWritePrivilege()?<Aaccountlist />: <ProvidersDashboard />  },
    
    { path: "accounts", element: hasWritePrivilege()?<Aaccountlist />:null },
    { path: "entries", element: hasWritePrivilege()?<Entrylist />:null },
    { path: "accounting-ledger", element: hasWritePrivilege()?<ComprobanteList />:null },
		{ path: "collect", element: <CobroList /> },
    { path: "pay", element: <ToPaylist /> },
    { path: "inventory", element: <InventoryDashboard /> },
    { path: "employees", element: <EmployeesDashboard /> },
    { path: "invoices", element: <InvoicesDashboard /> },
    { path: "providers-and-customers", element: <ProvidersDashboard /> },
    
    { path: "parameterize-balance", element: hasWritePrivilege()?<ParameterizeBG />:null },
    { path: "pay-checks-on-future", element: hasWritePrivilege()?<PayChecksOnFuturesDashboard />:null },
    
    { path: "notary", element:getStore("profileUser")?.hasNotaryAccess? <Customer/>: null },
    { path: "users-dashboard", element: getStore("isAdmin")? <UserDashboard />: null },
	]);
}




